import React, { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import styled from "styled-components";
import secureLocalStorage from "react-secure-storage";
import Passport from "../Image/passport-1.png";
import { useDispatch } from "react-redux";
import { reset } from "../../feature/EmploySlice";
import { setLogout } from "../../feature/UserSlice";
import { HiBars3 } from "react-icons/hi2";

const Navbar = () => {
  const [isClicked, setClicked] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleLogout = () => {
    navigate("/");
    secureLocalStorage.clear();
    dispatch(setLogout());
    dispatch(reset());
  };

  return (
    <Nav>
      <BrandContainer>
        <Brand src={Passport} alt="logo" />
        <BrandTitle>Visa Expiry Issue</BrandTitle>
      </BrandContainer>
      <BarIconContainer onClick={() => setClicked(!isClicked)}>
        <HiBars3 />
      </BarIconContainer>
      <Menu barclick={isClicked}>
        <Items to="dashboard" onClick={() => setClicked(false)}>
          Dashboard
        </Items>
        <Items to="employee" onClick={() => setClicked(false)}>
          Employees
        </Items>
        <LogoutButton1 onClick={handleLogout}>Logout</LogoutButton1>
      </Menu>
      <ButtonContainer2>
        <LogoutButton onClick={handleLogout}>Logout</LogoutButton>
      </ButtonContainer2>
    </Nav>
  );
};

export default Navbar;

export const Nav = styled.nav({
  width: "100%",
  height: "80px",
  display: "flex",
  flexWrap: "nowrap",
  msFlexDirection: "row",
  WebkitFlexDirection: "row",
  msWrapFlow: "nowrap",
  WebkitFlexWrap: "nowrap",
  background: "rgba(19, 15, 64,1.0)",
  padding: "0.5rem 2rem",
  position: "fixed",
  zIndex: 1,
});

export const BrandContainer = styled.div({
  height: "100%",
  display: "flex",
  flexWrap: "nowrap",
  WebkitFlexWrap: "nowrap",
  alignItems: "center",
  WebkitAlignItems: "center",
  gap: "0 0.5rem",
});
export const Brand = styled.img({
  width: "40px",
  height: "auto",
  background: "rgba(48, 51, 107,1.0)",
  padding: "0.3rem",
  borderRadius: "5px",
  boxShadow: "1px 1px 0 0 rgba(255, 255, 255, 0.2)",
});

export const BrandTitle = styled.span({
  color: "#fff",
  fontSize: "1.2rem",
  fontWeight: "600",
});

export const Menu = styled.div((props) => ({
  display: "flex",
  flexDirection: "row",
  flexWrap: "nowrap",
  WebkitFlexWrap: "nowrap",
  alignItems: "center",
  WebkitAlignItems: "center",
  background: "rgba(19, 15, 64,1.0)",
  gap: "0 0.5rem",
  margin: "0 auto",
  "@media (max-width: 992px)": {
    position: "absolute",
    content: "''",
    width: "100%",
    height: "100vh",
    top: 0,
    left: props.barclick === true ? 0 : "-2000px",
    right: 0,
    bottom: 0,
    margin: "auto",
    flexDirection: "column",
    gap: "1rem 0",
    marginTop: "80px",
    paddingBottom: "2rem",
    paddingTop: "30px",
    zIndex: "1",
    transition: "all ease 1s",
  },
}));

export const Items = styled(NavLink)({
  color: "#fff",
  fontSize: "1rem",
  fontWeight: "600",
  padding: "0.5rem 0.5rem",
  position: "relative",
  ":hover": {
    color: "blue",
    "::before": {
      position: "absolute",
      content: "''",
      width: "50%",
      height: "4px",
      border: "10px",
      background: "blue",
      bottom: "0",
      left: 0,
      right: "0",
      margin: "0 auto",
      transition: "all ease-in 2s",
    },
  },
});

export const LogoutButton = styled.button({
  padding: "0.5rem 1rem",
  border: "1px solid #fff",
  outline: "none",
  background: "transparent",
  color: "#fff",
  fontSize: "1rem",
  fontWeight: "600",
  ":hover": {
    background: "rgba(255, 71, 87,1.0)",
    border: "1px solid rgba(255, 71, 87,1.0)",
  },
});

export const LogoutButton1 = styled(LogoutButton)({
  display: "none",
  marginTop: "1rem",
  "@media (max-width: 992px)": {
    display: "block",
  },
});

export const ButtonContainer2 = styled.div({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  height: "100%",
  "@media (max-width: 992px)": {
    display: "none",
  },
});

export const BarIconContainer = styled.div({
  width: "50px",
  height: "50px",
  border: "1px solid #fff",
  borderRadius: "5px",
  position: "absolute",
  right: "20px",
  top: 0,
  bottom: 0,
  margin: "auto 0",
  color: "#fff",
  display: "none",
  alignItems: "center",
  justifyContent: "center",
  fontSize: "2rem",
  "@media (max-width: 992px)": {
    display: "flex",
  },
});
